@use '../../foundation/variables' as *;

.c-title {
  &__lv2 {
    font-size: 1rem;
    font-weight: 600;
    background-color: $text-color;
    color: white;
    border-radius: 6px;
    padding: 0.5rem 1rem;
    margin-bottom: 1rem;
  }

  // テーブルの見出し
  &__table {
    font-size: 1.25rem;
    font-weight: 500;
    color: $green;
    padding: 0.5rem 0;
  }

  // カレンダーの見出し
  &__calendar {
    font-size: 1.5rem;
    color: white;
    background-color: $green;
    line-height: 70px;
    text-align: center;
  }
} 